.main .img{height:400px;background-image:url("../../assets/img/archives/archive.webp");display:flex;justify-content:center;align-items:center;font-size:32px;font-weight:500;color:#fff}.main .archives{margin:20px auto;width:800px;padding:20px 10px}.main .archives span{color:#f09b38;display:flex;align-items:center;font-size:20px}.main .archives span i{display:flex;justify-content:center;align-items:center;width:40px;height:40px;border-radius:50%;background-color:#f09b38;margin-right:20px}.main .archives span i::before{font-size:36px;color:#fff}.main .archives .archive——box{display:flex;position:relative;cursor:pointer}.main .archives .archive——box>i:nth-child(1){display:block;width:15px;height:15px;background-color:#f7a77f;border-radius:50%;position:absolute;top:30px;left:10px}.main .archives .archive——box>i:nth-child(2){width:0;height:0;border:20px solid;border-color:rgba(0,0,0,0) rgba(0,0,0,0) #f5f5f5 rgba(0,0,0,0);transform:rotate(270deg);position:absolute;top:18px;left:31px}.main .archives .archive——box>div{width:100%;background-color:#f5f5f5;position:absolute;padding:15px 15px;top:12px;left:70px;font-size:18px;font-weight:700}